import React from 'react';
import { BrowserRouter as Router, Route, Link, Redirect, Switch } from 'react-router-dom';
import { Layout, Icon } from 'antd';
import * as types from './variables';
import logo from './asset/logo.svg';
import './App.css';

import IndexPage from './components/index/index';
import SearchPage from './components/search/search';

function App() {

  const state = {
    bgColor: types.BACKGROUND_WHITE
  }

  return (
    <Router>
      <Layout style={{ background: state.bgColor }}>
        <Layout style={{ background: state.bgColor }}>
          <div className='header-content'>
            <div className='header-logo'>
              <Link to='/'>
                <img src={logo}  className='header-logo Header-logo-spin Header-logo-spin' alt='Code Finder'/>
              </Link>
            </div>

            <div>
              {/* <Switch checkedChildren={ <Icon type='mech' /> } uncheckedChildren={ <Icon type='smile' /> } /> */}
            </div>
          </div>
        </Layout>

        <Switch>
          <Route exact path='/' component={IndexPage}/>
          <Route path='/search' component={SearchPage}/>
          <Redirect push to='/404' />
        </Switch>
      </Layout>
    </Router>
  );
}

export default App;



