import React from 'react';

const queryString = require('query-string');

export default function IndexPage(props) {

  function getCorrectQuery(s) {
    return queryString.parse(s.location.search).q;
  }

  return (
    <div>{ getCorrectQuery(props) }</div>
  )
}



